header{position:fixed;top:0;max-width:1920px;width:100vw;z-index:200}.сontaner{display:flex;justify-content:space-between;padding:30px 375px;align-items:center;background-color:rgba(16,16,29,.97)}@media only screen and (max-width: 1600px){.сontaner{padding:30px 175px}}@media only screen and (max-width: 1280px){.сontaner{padding:30px 75px}}@media only screen and (max-width: 767px){.сontaner{padding:30px}}@media only screen and (max-width: 600px){.сontaner{padding:30px 10px}}.сontaner__title{font-size:28px;font-weight:800;text-transform:uppercase;text-align:start;text-decoration:none;color:#fff}@media only screen and (max-width: 1280px){.сontaner__title{font-size:20px}}@media only screen and (max-width: 767px){.сontaner__title{font-size:18px}}.сontaner__title span{color:#42a9ed}.nav{display:flex;column-gap:60px;white-space:nowrap}@media only screen and (max-width: 767px){.nav{display:none}}.nav a{font-size:13px;font-weight:600;text-transform:uppercase;color:#fff;cursor:pointer;transition:all 400ms ease;text-decoration:none}.nav a:hover{color:#43abf0}